@import 'variables';

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
  font-size: 100%;
}
body {
  font-size: 0.875rem;
  font-family: "Be+Vietnam", sans-serif;

  h1,
  h2,
  h3,
  h4,
  h5{
    color:$DARKBLUE;
    font-size:  0.75rem;
  }
  p{
    color: $DarkGrayishBlue;
    font-size: 0.98rem;
  }
  img{
    max-width: 100%;
    height: auto;
  }

  @font-face {
    font-family: 'icomoon';
    src:  url('../fonts/icomoon.eot?mztwpf');
    src:  url('../fonts/icomoon.eot?mztwpf#iefix') format('embedded-opentype'),
      url('../fonts/icomoon.ttf?mztwpf') format('truetype'),
      url('../fonts/icomoon.woff?mztwpf') format('woff'),
      url('../fonts/icomoon.svg?mztwpf#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
  } 
  [class^="icon-"], [class*=" icon-"] {

    font-family: 'icomoon' !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

  
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  .icon-github:before {
    content: "\e900";
  }
  .icon-twitter:before {
    content: "\e901";
  }

}