@import 'variables';
@import 'global';
.container{
    //display: flex;
    background-color:$DARKBLUEE;
    height: 44vh;
    padding: 2.5625rem 6.6875rem;
    color: white;
   

    .flex-footer{
        display: flex;
        
        //align-items: center;
        justify-content: space-between;
        .footer-logos{
            display: flex;
            justify-content: space-evenly;
            flex-direction: column;
        }

        .footer-icons{
            display: flex;
            justify-content: space-between;
            list-style: none;
            margin-top: 4.0rem;

        }
        
        .list-items{
            color: white;
            list-style: none;
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;

            a{
                text-decoration: none;
                color: white;
                //list-style: none;
                padding: 15px;
                margin: 3rem;
                //text-transform: uppercase;
                font-size: 12px;
                //font-weight: 700;
                letter-spacing: 1px;
                transition: 0.2s;
            }
        }

        
    }
    .copyright{
        margin-top: 4rem;
        display: flex;

    }
    .attribution{
        display: flex;
        margin-top: 5rem;
        justify-content: center;
        text-align: center;
        margin-bottom: 0;
    }
}